<template>
  <div class="display">
    <section id="view" class="view">
      <div class="view-body">
        <h1 class="title">The World Wide Web is Yours!</h1>
        <i class="s"></i>
        <i class="s"></i>
        <i class="s"></i>
        <i class="s"></i>
        <i class="s"></i>
      </div>
    </section>
    <section class="taskbar">
      <div class="container">
        <search class="is-hidden-mobile"></search>
        <div class="tile is-parent">
          <div class="tile is-child">
            <div class="section share">
              <h2 class="title">Share</h2>
              <ul>
                <li><a href="//medium.com/@daetalus">Medium</a></li>
                <li><a href="//twitter.com/@daetalus">Twitter</a></li>
                <li><a href="//github.com/daetal-us">Github</a></li>
                <li><a href="//linkedin.com/in/daetalus">Linkedin</a></li>
                <li><a href="//instagram.com/daetal.us">Instagram</a></li>
              </ul>
            </div>
          </div>
          <div class="tile is-child">
            <div class="section work">
              <h2 class="title">Work</h2>
              <ul>
                <li><a href="//kuokoa.studio">Kuokoa Studio</a></li>
                <li><a href="//kuokoa.life">Kuokoa Life</a></li>
                <li><a href="//foodvib.es">FOOD VIBES</a></li>
              </ul>
            </div>
          </div>
          <div class="tile is-child">
            <div class="section read">
              <h2 class="title">Read</h2>
              <ul>
                <li v-for="r in shuffledReads"><a :href="r[1]">{{ r[0] }}</a></li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import Search from './components/Search';

const reads = [
  ['Hacker News', '//news.ycombinator.com'],
  ['Washington Post', '//washingtonpost.com'],
  ['Atlas Obscura', '//www.atlasobscura.com'],
  ['Nautilus', '//nautil.us'],
  ['Adbusters', '//adbusters.org'],
  ['Life & Thyme', '//lifeandthyme.com'],
  ['n+1', '//nplusonemag.com'],
  ['Civil Beat', '//civilbeat.org'],
  ['Greater Good', '//greatergood.berkeley.edu'],
  ['The Guardian', '//theguardian.com'],
];

export default {
  components: {
    Search,
  },
  computed: {
    shuffledReads() {
      return reads
        .map(r => ({ s: Math.random(), v: r }))
        .sort((a, b) => (a.s > b.s ? 1 : -1))
        .map(r => r.v);
    },
  },
};
</script>

<style lang="scss">
@import 'main.scss';
</style>



