<template>
    <form @submit.prevent="search" class="search">
      <b-input v-model="query"
        ref="search"
        placeholder="type and press enter to surf the web"
        type="search"
        autocomplete="off"
        autocorrect="off"
        autocapitalize="off"
        spellcheck="false"
        expanded>
      </b-input>
    </form>
</template>

<script>
const searchEngine = 'https://duckduckgo.com/?q=';

export default {
  data() {
    return {
      query: '',
    };
  },
  computed: {
    isUri() {
      if (/^(.*)\.(.*)$/.test(this.query)) {
        if (this.query.slice(0, 2) !== '//') {
          this.query = `//${this.query}`;
        }
        return true;
      }
      const a = document.createElement('a');
      a.href = this.query;
      return (a.host && a.host !== window.location.host);
    },
  },
  mounted() {
    this.$refs.search.focus();
  },
  methods: {
    search() {
      if (this.isUri) {
        window.location.href = this.query;
      } else {
        window.location.href = `${searchEngine}${this.query}`;
      }
    },
  },
};
</script>



